import { initializeApp } from 'firebase/app';
import { getAuth, sendSignInLinkToEmail } from 'firebase/auth';

initializeApp({
  apiKey: 'AIzaSyDDHltlwPJGgqYbVyQ83VV6HMwT5TqNFg8',
  authDomain: 'auth.rebelplans.com',
  projectId: 'rebel-plans',
  messagingSenderId: '29847344796',
  appId: '1:29847344796:web:2c601da3e5bfe5cd4a3f59',
});

const auth = getAuth();
const actionCodeSettings = {
  url: process.env.NODE_ENV === 'production' ? 'https://rebelplans.com/login/' : 'http://localhost:9000/login/',
  handleCodeInApp: true,
};

auth.onAuthStateChanged((user) => {
  if (user && user.email && user.emailVerified) {
    if (document.body.className === 'login') {
      window.location.href = actionCodeSettings.url;
    } else {
      // @ts-ignore
      window.redirect = () => {
        window.location.href = actionCodeSettings.url;
      };
    }
  }
});

document.querySelector('form')?.addEventListener('submit', (e) => {
  e.preventDefault();
  const email = (document.getElementById('email') as HTMLInputElement).value;
  // TODO: check if email has valid format
  sendSignInLinkToEmail(auth, email, actionCodeSettings).then(() => {
    window.localStorage.setItem('emailForSignIn', email);
    const form = document.forms[0];
    if (form) {
      form.setAttribute('disabled', '');
      form.querySelectorAll('input, button').forEach((el) => el.setAttribute('disabled', ''));
      (form.querySelector('.close') as HTMLAnchorElement).focus();
    }
  });
  // .catch((error) => {
  //   console.error(error);
  // });
});
